.formGroup {
    margin-bottom: 15px;
    display: grid;
    grid-template-columns: 70px 1fr;
    align-items: center;
    gap: 20px;
}

.field {
    padding: 10px 15px;
}

.btn {
    display: inline-block;
    background-color: grey;
    cursor: pointer;
    padding: 10px 15px;
    border-radius: 10px;
    color:black;
    margin-bottom: 30px;
    background: rgb(9, 73, 121);
        background: linear-gradient(193deg, rgba(9, 73, 121, 0.4150035014005602) 49%, rgba(0, 255, 252, 1) 100%);
        box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.btn:hover,
.btn:focus {
    background-color: blue;
}
.input {
    border-radius: 10px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    border: 2px solid rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;

}

.input:hover,
.input:focus {
    border-color: blue;
}